body{
    padding:100px;
    padding-top:0;
}

header{
display:flex;
justify-content: space-between;
align-items: center;
background-color:lightblue;
padding:0.5em;
border-left: 0.5em solid black;    
}

nav{
    background-color: aqua;
    box-shadow: 0 0px 10px black;
    font-size: 1.4em;
    position:absolute;
    right:0;
    top:0;
    height:100vh;
    width:250px;
    display:none;
}

nav ul{
    width:100%;
    list-style-type: none;
    text-align:center;
    margin:0 auto;
    padding:0;
}

nav li{
    padding:0.8em 0;
    width:100%;
}

nav a {
    color: white;
    text-decoration: none;
    padding: 0 1em;
}

nav li:hover {
    background-color:aquamarine;
}

    /*Öppna menyknapp*/
    .menu-btn-open{
        display:flex;
        flex-direction: column;
        justify-content: center;
        align-items:center;
        padding:15px 25px;
        background-color: orange;
        border: 2px solid white;
        cursor: pointer;
        margin-left:1em;
        box-shadow: 1px 1px 1px black;
    }

    .open{
        margin: 0 1em;
    }

    .menu-btn:hover{
        background-color: orangered;
        box-shadow: 0 0px 5px gainsboro;
    }


    /*bars*/
    .bars{
        display:block;
        width: 40px;
        height: 3px;
        background: white;
        margin: 5px;
    }

    .menu-text{
        font-size: 1.2em;
        color:white;
    }

    /*Stäng menyknapp*/
    .close{
        margin:1 em 1.5em;
        padding: 10px 15px;
    }

    /*Kryssbars*/
    .cross1{
        width:25px;
        transform: rotate(45deg);
        position:relative;
        top:5px;
    }

    .cross2{
        width:25px;
        transform: rotate(135deg);
        position:relative;
        top: -3px;
    }

    .breadcrumbs{
        display: flex;
        align-items: center;
    }

    footer{
        display: flex;
        justify-content: space-evenly;
        align-items: center;
    }
